import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table';
import { ShoppingCartService } from '../../services/shopping-cart.service';
import { ShoppingCartItem } from '../../models/firebase-objects/shopping-cart-item.interface';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Meta } from '@angular/platform-browser';


@Component({
    selector: 'app-shopping-cart',
    templateUrl: './shopping-cart.component.html',
    styleUrls: ['./shopping-cart.component.scss'],
})
export class ShoppingCartComponent implements OnInit, OnDestroy {
    columnsToDisplay = ['productImage', 'categoryName', 'productName', 'quantity', 'price'];
    dataSource: MatTableDataSource<ShoppingCartItem>;
    itemsSubscription: Subscription;
    totalPrice: number;
    totalQuantity: number;

    constructor(private cartService: ShoppingCartService,
        private router: Router,
        private spinner: NgxSpinnerService,
        private metaService: Meta) {

        this.addTag();

        this.dataSource = new MatTableDataSource<ShoppingCartItem>();
        this.cartService.initialize().then(() => {
            this.itemsSubscription = this.cartService
                .getAllItems()
                .subscribe(items => {
                    this.dataSource.data = items;
                    this.updateTotals();
                });
        });
    }
    addTag() {

        this.metaService.addTag({ name: 'title', content: 'BlackSushi - найбільші і найкращі суші та піца у Львові! ' });
        this.metaService.addTag({ name: 'description', content: 'Швидка і безкоштовна доставка ☎0630494941 ☎0930494941' });
        this.metaService.addTag({ property: 'og:title', content: 'Content Title for social media' });


        //  Open Graph / Facebook 
        this.metaService.addTag({ property: "og:type", content: 'website ' });
        this.metaService.addTag({ property: "og:url", content: 'http://blacksushi.com.ua/' });
        this.metaService.addTag({ property: "og:title", content: "BlackSushi - найбільші і найкращі суші у Львові! " });
        this.metaService.addTag({
            property: "og:description", content: "Замовляй найкращі суші у нас✅ Безкоштовна доставка суші✅ Замовити найсмачніші суші ☎0630494941 ☎0930494941"
        });
        this.metaService.addTag({ property: "og:image", content: "" });
    }

    ngOnInit() {
    }

    ngOnDestroy() {
        if (this.itemsSubscription) {
            this.itemsSubscription.unsubscribe();
        }
    }

    private updateTotals() {
        this.totalQuantity = this.dataSource.data.reduce(
            (total, item) => (total += item.quantity),
            0
        );

        this.totalPrice = this.dataSource.data.reduce(
            (total, item) => (total += item.quantity * item.product.price),
            0
        );
    }

    onQuantityPlusOne(item: ShoppingCartItem) {

        this.cartService.addToCart(item.product);
    }

    onQuantityMinusOne(item: ShoppingCartItem) {
        this.cartService.removeFromCart(item.product.id);
    }

    onCheckOut() {
        // create an order
        // add it to the order collection
    }


    backToMain() {
        this.spinner.show();

        setTimeout(() => {
            /** spinner ends after 1 seconds */
            this.spinner.hide();
        }, 1000);
        this.router.navigate(['/']);
    }

}
